<template>
  <div>

    <section class="active_header">
      <div class="container">

        <div class="active_slider">
          <div class="main_bg_images">
            <div class="about_platform">
              <div>
                <div class="plac_tit">
                  {{$t('platform_best_students')}}
                </div>
                <div class="plat_des">
                {{$t('find_your_future_profession_offer')}}
                </div>
              </div>
            </div>
            <img src="../assets/images/backstar.png" alt="">
          </div>

          <VueSlickCarousel ref="carousel1" v-if="user_active.results.length" v-bind="settings">
            <div v-for="(item,users) in user_active.results" :key="users" class="main_section_active_father">

              <div class="slider_main">
                <div class="main_f_c">
                  <div class="slider_int_img">
                    <img v-if="item.profile_image" :src="item.profile_image" width="100%"  alt="">
                    <img v-else src="../assets//images//user_team.png" width="100%"  alt="">
                  </div>
                  <div class="slider_text">
                    {{item.full_name}}
                  </div>
                </div>
              </div>
            </div>
          </VueSlickCarousel>
          <div class="arrow_fathers">
            <div class="d-flex">
              <button  @click="showPrev1()">
                <img src="../assets/images/arrowleft.png">
              </button>
              <button @click="showNext1()">
                <img src="../assets/images/arrowright.png">
              </button>
            </div>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import VueSlickCarousel from 'vue-slick-carousel'
import 'vue-slick-carousel/dist/vue-slick-carousel.css'
// optional style for arrows & dots
import 'vue-slick-carousel/dist/vue-slick-carousel-theme.css'
export default {
  components: {
    VueSlickCarousel
  },
  data(){
    return{
      settings:{
        "dots": false,
        "focusOnSelect": false,
        "infinite": false,
        "speed": 500,
        "slidesToShow": 5,
        "slidesToScroll": 4,
        "touchThreshold": 5,
        "responsive": [
          {
            "breakpoint": 1024,
            "settings": {
              "slidesToShow": 3,
              "slidesToScroll": 3,
              "infinite": true,
              "dots": true
            }
          },
          {
            "breakpoint": 600,
            "settings": {
              "slidesToShow": 1,
              "slidesToScroll": 1,
              "initialSlide": 1
            }
          },
          {
            "breakpoint": 480,
            "settings": {
              "slidesToShow": 1,
              "slidesToScroll": 1
            }
          }
        ]
      },
      user_active:[]
    }
  },
  mounted() {
    this.$axios.get('/account/students/activiest/', {
              headers: {
                'Accept-Language':localStorage.getItem('lang')
              }
            }
    )
            .then(res=>{
              this.user_active=res.data
            })
  },
  methods:{
    showNext1() {
      this.$refs.carousel1.next()
    },
    showPrev1() {
      this.$refs.carousel1.prev()
    },
  }
}
</script>
