<template>
  <div>
    <div class="videos_containers" v-if="hide_shows">
      <button @click="hide_shows=false">

        <svg width="36" height="36" viewBox="0 0 36 36" fill="none" xmlns="http://www.w3.org/2000/svg">
          <path d="M0 18C0 8.05888 8.05888 0 18 0C27.9411 0 36 8.05888 36 18C36 27.9411 27.9411 36 18 36C8.05888 36 0 27.9411 0 18Z" fill="#F3F3F3"/>
          <path d="M13 23L23 13M13 13L23 23" stroke="#333231" stroke-width="1.8" stroke-linecap="round" stroke-linejoin="round"/>
        </svg>

      </button>
      <iframe width="100%" height="100%" src="https://www.youtube.com/embed/9Aq3z_FARBc?autoplay=1&&mute=1"
              title="YouTube video player" frameborder="0"
              allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
              allowfullscreen></iframe>


    </div>
      <div class="container">
        <div class="section_one">
            <div class="row">
              <div class="col-xl-6">
                <div class="sec_one_title">
                 {{$t('find_your_future_profession')}}
                </div>
                <div class="sec_one_desc">
                  {{$t('platform_info')}}
                </div>
                <div class="main_links_sec">
                  <router-link to="/test_category" class="tests">
                    {{$t('via_tests')}}
                  </router-link>
                  <router-link to="/calculation" class="test_calck">
                    {{$t('calculator_link')}}
                  </router-link>
                </div>
                  <div class="banner_fathe">

                      <div class="acive_students">
                          <div class="ac_st">
                              {{$t('active_users')}}
                          </div>
                          <div class="ac_count">
                              {{banner.total_active_students.toLocaleString()}} +
                          </div>
                      </div>
                      <div class="youtube_frame">
                        <div class="dark_shadows" @click="hide_shows=true"></div>
                          <div class="video_shadow my_video">
                              <iframe width="100%" height="100%" src="https://www.youtube.com/embed/9Aq3z_FARBc?autoplay=1&&mute=1"
                                      title="YouTube video player" frameborder="0"
                                      allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
                                      allowfullscreen></iframe>

                          </div>
                      </div>
                  </div>
              </div>
              <div class="col-xl-6">
                <div class="sec_images">
                  <img src="../assets/images/bgimg.png" alt="">
                </div>
              </div>
            </div>
        </div>
        <div class="src_service">
          <div class="row">
            <div class="col-xl-4" v-for="(item,indes) in services" :key="indes">
              <div :class="`ser_cars_sec order_class${item.order}`" >
                  <div class="sec_ser_tit">
                    {{item.title}}
                  </div>
                  <div class="sec_ser_desc">
                    {{item.description}}
                  </div>
              </div>
            </div>
          </div>
          <div class="mains_videos " autocapitalize="on">
            <video class="border-radiuss" width="100%" height="100%" autoplay muted loop  >

              <source :src="`${videos}`" type="video/mp4" />


            </video>

          </div>
        </div>
      </div>
  </div>
</template>
<script>
    export default {
        data(){
            return{
                services:[],
                banner:[],
                videos:'videoss.mp4',
                hide_shows:false
            }
        },
        mounted() {
            this.$axios.get('/v1/services/', {
                    headers: {
                        'Accept-Language':localStorage.getItem('lang')
                    }
                }
            )
            .then(res=>{
                this.services=res.data
            })
            this.$axios.get('/v1/index-banner/', {
                    headers: {
                        'Accept-Language':localStorage.getItem('lang')
                    }
                }
            )
                .then(res=>{
                    this.banner=res.data
                })
        },

    }
</script>