<template>
  <div class="sectionsex">
    <div class="container">
      <router-link to="/university" class="decoration_none">
      <div class="sex_title">
        {{$t('local_and_international_universities')}}
      </div>
      </router-link>
      <div class="d-flex justify-content-between align-items-center">
        
        <div class="sex_desc">
          {{$t('learn_about_foreign_universities')}}
        </div>
        <div class="sex_slider">
          <div class="arrow_fathers">
            <div class="d-flex">
              <button @click="showPrev()">
                <img src="../assets/images/arrowleft.png">
              </button>
              <button @click="showNext()">
                <img src="../assets/images/arrowright.png">
              </button>
            </div>
          </div>
        </div>
      </div>
      <div>
        <VueSlickCarousel ref="carousel" v-if="univers.results.length" v-bind="settings">

          <div   v-for="(item,ind) in univers.results" :key="ind" class="sex_card">
            <a class="text-decoration-none" :href="item.link">
              <div class="sec_sl_card">
              <div class="sex_sl_img">
                <img src="../assets/images/close.jpg" alt="">
              </div>
              <div class="sex_card_title">
                {{item.title}}
              </div>
              <div class="sex_card_location">

               <div>
                 <svg width="20" height="20" viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg">
                   <path fill-rule="evenodd" clip-rule="evenodd"
                         d="M6.93148 14.4387C5.24439 13.0716 3.33301 11.5228 3.33301 8.33301C3.33301 4.6499 6.31759 1.66675 9.99927 1.66675C13.6809 1.66675 16.6655 4.65133 16.6655 8.33301C16.6655 11.5288 14.7552 13.0795 13.068 14.4489C11.8224 15.4601 10.6984 16.3725 10.5548 17.7752L10.5486 17.8358C10.5197 18.1179 10.2821 18.3324 9.99851 18.3324C9.71401 18.3324 9.47615 18.1161 9.44924 17.8329L9.44375 17.7752C9.31025 16.3662 8.18254 15.4525 6.93148 14.4387ZM10.0004 10.694C11.1509 10.694 12.0835 9.76129 12.0835 8.61077C12.0835 7.46025 11.1509 6.52756 10.0004 6.52756C8.84983 6.52756 7.91714 7.46025 7.91714 8.61077C7.91714 9.76129 8.84983 10.694 10.0004 10.694Z"
                         fill="#323133"/>
                 </svg>
               </div>
                Manzil:
                {{item.address}}
              </div>
              <div class="sex_card_location call_icon">


               <div>
                 <svg width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg">
                   <path
                           d="M0 1C0 0.734784 0.105357 0.48043 0.292893 0.292893C0.48043 0.105357 0.734784 0 1 0H3.153C3.38971 0.000108969 3.6187 0.0841807 3.79924 0.23726C3.97979 0.390339 4.10018 0.602499 4.139 0.836L4.879 5.271C4.91436 5.48222 4.88097 5.69921 4.78376 5.89003C4.68655 6.08085 4.53065 6.23543 4.339 6.331L2.791 7.104C3.34611 8.47965 4.17283 9.72928 5.22178 10.7782C6.27072 11.8272 7.52035 12.6539 8.896 13.209L9.67 11.661C9.76552 11.4695 9.91994 11.3138 10.1106 11.2166C10.3012 11.1194 10.5179 11.0859 10.729 11.121L15.164 11.861C15.3975 11.8998 15.6097 12.0202 15.7627 12.2008C15.9158 12.3813 15.9999 12.6103 16 12.847V15C16 15.2652 15.8946 15.5196 15.7071 15.7071C15.5196 15.8946 15.2652 16 15 16H13C5.82 16 0 10.18 0 3V1Z"
                           fill="#323133"/>
                 </svg>
               </div>

                {{item.phone_number}}
              </div>
            </div>
            </a>
          </div>
        </VueSlickCarousel>

    </div>

      <div class="more_link">
        <router-link to="/university">
          {{$t('see_all')}}
        </router-link>
      </div>
    </div>
    <div class="container">
      <div class="sex_title news_t">
        {{$t('news')}}
      </div>
      <div class="d-flex justify-content-between align-items-center">
        <div class="sex_desc news_d">
          {{$t('newss')}}
        </div>
      </div>

      <div class="news_cards">
        <div class="row">

          <div class="col-xl-4" v-for="(item,index) in data_main.results" :key="index">
            <router-link :to="`/news/${item.slug}`">
              <div class="news_card">
                <div class="news_img">
                  <img :src="item.cover_url" alt="">
                </div>
                <div class="news_title">
                  {{item.title}}
                </div>

                <!--                                    <div class="news_date">-->
                <!--                                        10-iyun, 2023  12:36-->
                <!--                                    </div>-->
              </div>
            </router-link>
          </div>
        </div>
        <div class="more_link">
          <router-link to="/news">
            {{$t('see_all')}}
          </router-link>
        </div>
      </div>

    </div>
  </div>
</template>

<script>
import VueSlickCarousel from 'vue-slick-carousel'
import 'vue-slick-carousel/dist/vue-slick-carousel.css'
// optional style for arrows & dots
import 'vue-slick-carousel/dist/vue-slick-carousel-theme.css'

export default {
  components: {
    VueSlickCarousel
  },
  data() {
    return {
      settings: {
        "dots": false,
        "focusOnSelect": false,
        "infinite": false,
        "speed": 500,
        "slidesToShow": 3,
        "slidesToScroll": 3,
        "touchThreshold": 3,
        "responsive": [
          {
            "breakpoint": 1024,
            "settings": {
              "slidesToShow": 3,
              "slidesToScroll": 3,
              "infinite": true,
              "dots": true
            }
          },
          {
            "breakpoint": 600,
            "settings": {
              "slidesToShow": 1,
              "slidesToScroll": 1,
              "initialSlide": 1
            }
          },
          {
            "breakpoint": 480,
            "settings": {
              "slidesToShow": 1,
              "slidesToScroll": 1
            }
          }
        ]

      },
      data_main:[],
      univers:[]
    }
  },
  mounted() {
      this.$axios.get(`/v1/top-web-news/`,{
        headers:{
          'Accept-Language':localStorage.getItem('lang')
        }
      })
      .then(res => {
        this.data_main = res.data
        this.show_cards=true
      })
    this.$axios.get(`/university/`,
            {
              headers:{
                'Accept-Language':localStorage.getItem('lang')
              }
            })
            .then(res => {
              this.univers = res.data
              this.show_cards=true
            })
  },
  methods:{
    showNext() {
      this.$refs.carousel.next()
    },
    showPrev() {
      this.$refs.carousel.prev()
    },
  }
}
</script>